.dark-button {
    @include width-and-height( 100%, auto );
    @include display-flex;
    overflow: hidden;
    border-radius: 6px;
    border: 0;
    background-color: $secondary;
    text-align: center;
    cursor: pointer;
    font: normal normal bold 14px/normal Lato;
    color: $main;
    transition: background-color ease .3s;

    &:hover {
        background-color: $highlight;
    }
}

.stars-1,
.stars-2,
.stars-3,
.stars-4,
.stars-5 {
    font-family: 'corebiz-icons';

    &::before {
        color: $highlight;
    }

}

.stars-1 {
    &::before {
        content: '\e802 \00a0 \e801 \00a0 \e801 \00a0 \e801 \00a0 \e801';
    }
}

.stars-2 {
    &::before {
        content: '\e802 \00a0 \e802 \00a0 \e801 \00a0 \e801 \00a0 \e801';
    }
}

.stars-3 {
    &::before {
        content: '\e802 \00a0 \e802 \00a0 \e802 \00a0 \e801 \00a0 \e801';
    }
}

.stars-4 {
    &::before {
        content: '\e802 \00a0 \e802 \00a0 \e802 \00a0 \e802 \00a0 \e801';
    }
}

.stars-5 {
    &::before {
        content: '\e802 \00a0 \e802 \00a0 \e802 \00a0 \e802 \00a0 \e802';
    }
}